ul,li {
  list-style: none;
  margin:0;
  padding:0;
}

.list-item-blankCell {
    z-index: 0;
  width: 100%;
  height: 100%;
  background-color: #fffde7;
  border-top: rgb(0, 0, 0, 0.2) 0.05rem solid;
  border-bottom: rgb(0, 0, 0, 0.2) 0.05rem solid;
  border-right: rgb(0, 0, 0, 0.1) 0.05rem solid;
  border-left: rgb(0, 0, 0, 0.1) 0.05rem solid;
  color: rgba(255, 255, 255, 0.95);
  padding: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.li-fillingCell {
  width: 100%;
  height: 100%;
  border-radius: 0.3rem;
  background-color: #00b8d4;
  border-top: rgb(0, 0, 0, 0.2) 0.05rem solid;
  border-bottom: rgb(0, 0, 0, 0.2) 0.05rem solid;
  border-right: rgb(0, 0, 0, 0.1) 0.05rem solid;
  border-left: rgb(0, 0, 0, 0.1) 0.05rem solid;
  color: rgba(255, 255, 255, 0.95);
  z-index:1;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  cursor: pointer;
  font-family: "Kosugi Maru", sans-serif;
  font-weight: bold;
  flex-flow: column wrap;
}

.box-scheduleHeader {
  width: 100%;
  height: 5rem;
  position: sticky;
  top: 0;
  background-color: #000a12;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.item-scheduleHeader {
  color: rgba(255, 255, 255, 0.95);
}
.box-schedules {
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 90vh;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  overflow: scroll;
}
.box-scheduleRow {
  width: 12%;

}
.box-scheduleRow:hover{
    background-color: red;
    
}
.li-blankCell {
  width: 100%;
  height: 5rem;
  background-color: #fffde7;
  border-top: rgb(0, 0, 0, 0.2) 0.05rem solid;
  border-bottom: rgb(0, 0, 0, 0.2) 0.05rem solid;
  border-right: rgb(0, 0, 0, 0.1) 0.05rem solid;
  border-left: rgb(0, 0, 0, 0.1) 0.05rem solid;
  color: rgba(255, 255, 255, 0.95);
  padding: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  
}
.item-fillingCell{
    width: 100%;
}

.box-clockLine {
  width: 100%;
  height: 1px;
  background: #dd2c00;
  position: relative;
}
.cell-under {
    z-index:0;
  width:100%;
  pointer-events: none;
}
